<template>
  <v-app>
      <h1 class="products">내 클래스</h1>
      <v-container class="my-5">
        <v-layout row wrap>
          <v-flex xs12 sm6 md3 v-for="(productItem,index) in classlist" :key="index">
            <div class="cards">
              <ProductItems :productItem="productItem"/>
            </div>
          </v-flex>
          <v-flex xs12 sm6 md3>      
            <v-card flat class="ma-1 text-xs-center" @click="show">
              <div class="ctitle">
                <v-img src="../assets/images/plus.png" aspect-ratio="1" class="cimg">
              </v-img>
              </div>
            </v-card>
          </v-flex>
        </v-layout>
      </v-container>
      <modal name="modal-classCreate" height="700px" width="700px" @before-open="beforeOpen">
      <form id="file-upload-form" class="uploader" style="margin-top:40px;" enctype="multipart/form-data">
      <div id="classcreatemodal">
        <div class="classcreatemodaldiv1">
          <h2>새 클래스 추가</h2>
        </div>
        <div class="classcreatemodaldiv2">
          <div v-if="!classimg">
            <div class="noimg">
              <label for="photo" id="file-drag-not_img" ref="dropbox">
                <h4>클래스 이미지 등록</h4>
                <input type="file" id="photo" accept="image/*" @change="onFileChange"/>
              </label>
            </div>
          </div>
          <div v-else>
            <div class="haveimg">
              <label class="file-upload" for="photo" id="file-drag" ref="dropbox">
                <img :src="classimg" class="img_upload"/>
              </label>
              <input type="file" id="photo" accept="image/*" @change="onFileChange"/>
            </div>
          </div>
          <div>
            <div>
              <div>
                <label for="classname" class="floatleft">클래스이름 : </label>
              </div>
              <div class="floatleft">
                <b-form-input v-model="classname" id="classname" type="text" placeholder="클래스 이름을 입력해주세요."/>
              </div>
            </div>
            <div>
              <label for="userlimit" class="floatleft">최대 인원 : </label>
              <b-form-input v-model="userlimit" id="userlimit" type="text" maxlength="2" placeholder="10"/>
            </div>
            <div>
              <label for="teenager" class="floatleft">수강 연령 : </label>
              <b-form-select v-model="targetage" id="teenager" :options="age"/>
            </div>
          </div>
        </div>
        <div class="classcreatemodaldiv3">
          <div>
            <label for="classdesc" class="classdescl floatleft">클래스설명 : </label>
          </div>
          <div>
            <b-form-textarea v-model="classdesc" id="classdesc" placeholder="클래스에 간략한 설명을 적어주세요"/>
          </div>
        </div>
        <!-- <br>
        <label for="opentype">공개 여부 : </label>
        <input v-model="classdesc" id="opentype" type="textarea" placeholder="10"/> -->
        <div class="classcreatemodaldiv4">
          <div>
            <label for="starttime" class="floatleft">수업 기간 : </label>
          </div>
          <div>
            <input v-model="startdate" id="starttime" type="date"/>
            ~
            <label for="endtime"></label>
            <input v-model="enddate" id="endtime" type="date"/>
          </div>
        </div>
        <div class="classcreatemodaldiv5">
          <b-button class="btn" @click="createclass">개설</b-button>
        </div>
      </div>
      </form>
    </modal>
  </v-app>
</template>

<script>
import ProductItems from "@/components/ProductItems";
import axios from "axios";

export default {
  
    data() {
        return {
            classname:"",
            classdesc:"",
            targetage:null,
            userlimit:"",
            opentype:"public",
            startdate:"",
            enddate:"",
            classimg:"",
            userInfo : this.$store.getters.userInfo,

            fileName: "/img/class/default.jpg",
            files: "",

            userno:"1",
            
            classlist: [],

            age: [
              {value: null, text: "연령대"},
              {value: 0, text:"유치원생"},
              {value: 1, text:"초등학생(저학년)"},
              {value: 2, text:"초등학생(고학년)"},
              {value: 3, text:"중학생"},
              {value: 4, text:"고학생"},
              {value: 5, text:"대학생"},
              {value: 6, text:"기타"},
            ],
            productList: [
                {
                    id: 1,
                    name: "초등국어 듣고 쓰기",
                    description: "Rookie Aviator Patched Bomber",
                    classimg: "https://cdn.vuetifyjs.com/images/cards/desert.jpg"
                },
                {
                    id: 2,
                    name: "초등국어 읽기",
                    description: "Rookie Aviator Patched Bomber",
                    classimg: "https://cdn.vuetifyjs.com/images/cards/desert.jpg"
                },
                {
                    id: 3,
                    name: "초등독서",
                    description: "Skinny Fit Oxford Shirt In White",
                    classimg: "https://cdn.vuetifyjs.com/images/cards/desert.jpg"
                },
                {
                    id: 4,
                    name: "Asos",
                    description: "slim shirt with stretch in blue",
                    classimg: "https://cdn.vuetifyjs.com/images/cards/desert.jpg"
                },
            ]
        };
    },
    methods:{
      show(){
        this.$modal.show("modal-classCreate");
      },
      createclass(){
        console.log(
          this.classname +","+  
          this.classdesc +","+
          this.targetage +","+
          this.userlimit +","+
          this.opentype +","+
          this.startdate +","+
          this.enddate +","+
          this.classimg +","+
          this.fileName //+","+
          // this.files
        );
        // let data = new JSON();
        // data.append('class_name',this.classname);
        // data.append('class_desc',this.classdesc);
        // data.append('target_age',this.targetage);
        // data.append('user_limit',this.userlimit);
        // data.append('open_type',this.opentype);
        // data.append('start_date',this.startdate);
        // data.append('end_date',this.enddate);
        // data.append('class_img',this.fileName);
        
        // data.append('user_no',this.userno);
        let data = {
        'className' : this.classname,
        'classDesc' : this.classdesc,
        'targetAge' : this.targetage,
        'userLimit' : this.userlimit,
        'openType' : this.opentype,
        'startDate' : this.startdate,
        'endDate' : this.enddate,
        'classImg' : this.fileName,
        'userNo' : this.userno,
        }
        
        let config ={
          header : {
            'Content-Type' : 'application/json',
            // 'Content-Type' : 'multipart/form-data',
            // 'Authorization' : 'JWT' + localStorage.getItem("token")
          }
        };
        // axios.post('http://k3d206.p.ssafy.io:8000/accounts/',data,config).then(
        axios.post('http://localhost:9999/class/class',data,config).then( //클래스 생성
          res =>{
            res
            this.$store.state.createclassval = 1
            // this.hide()
            this.$store.state.loadingflag = true;

            let configimg ={
            header : {
              'Content-Type' : 'multipart/form-data',
              // 'Authorization' : 'JWT' + localStorage.getItem("token")
              }
            };
            let form = new FormData();
            form.append("file",this.files)
            form.append("userNo",this.userno)
            form.append("classNo",0)
            axios.put('http://localhost:9999/class/imgclass',form,configimg) //이미지업데이트
            .then((data) =>{
              data
              this.intervalid = setInterval(()=>{
              this.$store.state.loadingflag = false;
              location.reload()
          }, 2000);
            }).catch((error)=>{
              console.log(error)
            })

          })
          .catch( err => {
            console.log(err)
          })

      },


      beforeOpen(){
        this.classimg = "";
      },
      onFileChange(e) {
        //이미지 첨부
        console.dir(e)
        var files = e.target.files || e.dataTransfer.files;

        if (!files.length) return;
        // console.log(files[0])
        // var check = /[ㄱ-ㅎ|ㅏ-ㅣ|가-힣]/;
        // if(check.test(files[0].name)) {
        //   this.classimg="";
        //   this.hide();
        //   alert('한글이 포함되지 않은 이미지로 올려주세요');
        //   return;
        // }

        this.createImage(files[0]);
        this.files = e.target.files[0];
      },
      createImage(file) {
        //이미지 가져오기
        // console.log('경로'+file.name)
        this.fileName = file.name;
        var reader = new FileReader();
        var vm = this;

        reader.onload = (e) => {
          vm.classimg = e.target.result;
        };
        reader.readAsDataURL(file);
      },
      removeImage: function() {
        this.classimg = "";
        this.fileName = "";
      },
      userFail: function(text) {
        if (text[0] == "0") {
          text = text.substring(1, 5);
          this.userlimit = text;
        }
      },
      getClass(){
            axios.get('class/userclass')
            .then( res => {
                this.classlist = res.data;
            })
            .catch( err => {
                console.log(err)
            })
        },
    },
    created(){
      this.getClass();
      if(this.$session.get('jwt')==null){
        alert("잘못된 접근입니다.")
        this.$router.push('/');
      }
    },
    watch: {
      userlimit: function() {
      this.userFail(this.userlimit);
      },
    },
    components: {
        ProductItems
        }
    }
</script>


<style scoped>

#classcreatemodal *{
  margin: 0;
}
#classcreatemodal{
  margin: 0 10px;
}
#classcreatemodal>div{
  padding: 6px 0;
}
#teenager,#userlimit,#classname{
  width:190px;
  margin: 6px 0;
  display: inline-block;
}
/* #userlimit{
  width: 190px;
} */
.floatleft{
  float:left;
  padding: 0 0 0 10px;
  line-height: 50px;
}
.classcreatemodaldiv2,
.classcreatemodaldiv4{
  display: flex;
  line-height: 50px;
}
#starttime,#endtime{
  border:solid 1px;
  border-color:rgb(200, 200, 200);
  border-radius: 5px;
}
#classname{
  width: 270px;
}
#classdesc{
  resize: none;
  height: 90px;
}

#photo{
  display: none;
}
.haveimg,
.img_upload,
.noimg,
.photo{
  height: 200px;
  width: 200px;
  margin:0px auto;
  line-height: 200px;
}
.noimg{
  border-style:dashed;
  background:violet;
}
.cards{
  padding: 1% 1%;
  border-radius: 50%;
}

.cimg{
    border-radius: 10%;
}
.cfont{
    font-size:1vh;
}

.cti{
    
    border-radius: 15%;
    background-color: rgb(196, 189, 189);
    height: 5vh;
}
</style>
