<template>
    <v-card flat class="ma-1 text-xs-center">
        <div class="ctitle">
        <v-img :src="url + productItem.classImg" aspect-ratio="1" class="cimg">
            
        </v-img>
            <div class="cti">
                <v-card-title primary-title>
                    <h3 class="cfont headline pink--text text--accent-2 text-truncate"> 
                        {{productItem.className}}</h3>
                <!-- <div>{{productItem.description}}</div> -->
                </v-card-title>
            </div>
            <v-btn @click="modifyclass(productItem.classNo)">수정</v-btn>
            <v-btn @click="deleteclass(productItem.classNo)">삭제</v-btn>
        <!-- <v-card-actions>
          <v-btn flat color="orange">Add to Cart</v-btn>
        </v-card-actions> -->
        
        </div>
    </v-card>
</template>

<script>
import axios from 'axios'
    export default {
      props: ["productItem"],
      data() {
        return {
            url: this.$store.getters.BACKEND_URL
        };
      },
      methods : {
          modifyclass(classno) {
              classno
              this.$router.push(`/class/modify/${classno}`);
          },
          deleteclass(classno) {
              axios.delete(`class/oneclass/${classno}`)
              .then((response)=>{
                  response
                  alert('클래스가 삭제되었습니다.')
                  this.$router.go();
              })
              .catch((err)=>{
                  err
              })
          }
      }
    };
</script>

<style>
.cimg{
    border-radius: 10% 10% 0 0;
}
.cfont{
    font-size:20px;
}
.ctitle{
    
}
.cti{
    
    background-color: rgb(196, 189, 189);
    height: 80%;
}
</style>